import helper from '../../common/helper.js';
import drawQrcode from '../../common/weapp-qrcode.js';
var _self;
export default {

	data() {
		return {
			shows: 0,
			rwArray: {},
			rwbzArray: {},
			dateDes: "",
			TaskId: "",
			lqid: '',
			imageSrc: '',
			img: '',
			date: '',
			Merimg: [],
			merimg: [],
			index: '',
			pictures: ''
		}
	},
	onLoad(e) { //e接收任务的id
		// _self = this;
		// this.lqrwlist(e);
		// this.lqid = e.lqid;
		// this.qrcode()

	},
	methods: {
		// 生成二维码
		qrcode() {
			drawQrcode({
				width: 150,
				height: 150,
				canvasId: 'myQrcode',
				text: this.lqid
			})
			this.index = '0'
		},
		// 领取任务列表
		lqrwlist(e) {
			var endtime = this.data;
			helper.islogin(true);
			uni.showLoading({
				title: "加载中....",
				mask: true
			})
			uni.request({
				url: helper.websiteUrl + 'task/getmytask',
				method: "GET",
				data: helper.postdata({
					id: e.lqid,
					"userid": helper.getstate().userid,
				}),
				success: (res) => {
					if (res.data.code == 200) {
						this.TaskId = res.data.data.baseinfo.TaskId;
						this.date = res.data.data.baseinfo.Receivedate;
						this.Merimg = res.data.data.baseinfo.Pictures.split(';')
						var endtimes = this.date.replace(/\-/g, "/")
						var datetest = new Date(endtimes); //获取当前任务的结束时间 
						var Month = datetest.getMonth() + 1;
						var lazyday = parseInt(parseInt(datetest.getTime() - new Date().getTime()) / (24 * 3600 * 1000)) + 1; //获取剩余天数
						res.data.data.baseinfo.Receivedate = datetest.getFullYear() + "年" + Month + "月" +
							datetest.getDate() +
							"日"; //格式化任务结束时间
						if (lazyday > 0) //判断任务时间是否到期 显示不同的内容
						{
							this.dateDes = lazyday + "天后，即可免费享用霸王餐";
						} else {
							this.dateDes = "该活动已经结束！";
						}
						this.rwArray = res.data.data.baseinfo;
						this.rwbzArray = res.data.data.baseinfo.Instruction.split(";");
					} else {
						helper.goout(res.data.info);
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
					uni.hideLoading();
				},
				fail: (res) => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请返回重试！'
					});
				}
			})
		},
		// 查看缩略图
		openMerimg() {
			uni.previewImage({
				urls: this.merimg
			});
		},
		// 放弃任务
		fanqibut() {
			uni.showModal({
				title: '提示',
				content: '是否放弃该任务！',
				success: function(res) {
					if (res.confirm) {
						uni.request({
							url: helper.websiteUrl + 'task/submitreview',
							method: 'GET',
							data: helper.postdata({
								"taskid": _self.TaskId,
								"id": _self.lqid,
								"userid": helper.getstate().userid,
								"type": "2",
								"IsGet": '0'
							}),
							success: res => {
								if (res.data.code == 200) {
									uni.navigateBack({
										delta: 1
									});
								}
							},
							fail: () => {
								uni.showToast({
									icon: 'none',
									title: '网络异常,请重试！'
								});
								uni.hideToast()
							},
						});
					}
				}
			});
		},
		setData: function(obj) {
			let that = this;
			Object.keys(obj).forEach(function(key) {
				that.$set(that.$data, key, obj[key])
			});
		},
		scbut() {
			var _this = this
			if (_this.merimg.length >= "2") {
				uni.showToast({
					icon: 'none',
					title: '截图最多上传两张',
				});
			} else {
				uni.chooseImage({
					count: 1,
					sizeType: ['original', 'compressed'],
					sourceType: ['album'],
					success: function(res) {
						uni.showLoading({
							title: '上传中'
						});
						uni.uploadFile({
							url: helper.websiteUrl + '/annexes/UploadImg',
							filePath: res.tempFilePaths[0],
							fileType: "uploadFile",
							name: 'upload',
							formData: helper.postdata({
								"folderId": "123465",
							}),
							success: function(resup) {
								var resdata = JSON.parse(resup.data);
								if (resdata.code == 200) {
									var url = 'https://cszx.yiruit.net';
									var imgurl = url + resdata.info;
									_this.img = imgurl;
									uni.showToast({
										icon: 'none',
										title: '上传成功！'
									});
								} else {
									uni.hideLoading();
									uni.showToast({
										icon: 'none',
										title: resdata.info
									});
								}
								uni.hideLoading();
								_this.merimg.push(_this.img);
							},
							fail() {
								uni.hideLoading();
								uni.showToast({
									icon: 'none',
									title: '识别失败'
								});
							}
						});
						_this.setData({
							img: res.tempFilePaths[0],
						});
					},
				})
			}

		},
		// 任务提交按钮
		tjbut() {
			if (_self.dateDes == "该活动已经结束！") {
				uni.showToast({
					icon: 'none',
					title: "该活动已结束！"
				})
			} else if (_self.merimg == '') {
				uni.showToast({
					icon: 'none',
					title: "请上传截图！"
				})
			} else {
				_self.pictures = _self.merimg.join(';');
				uni.showLoading({
					title: "任务提交中...",
				})
				uni.request({
					url: helper.websiteUrl + 'task/submitreview',
					method: 'GET',
					data: helper.postdata({
						"id": _self.lqid,
						"pics": _self.pictures,
						"userid": helper.getstate().userid,
						"type": 1,
						"taskid": _self.TaskId,
					}),
					success: res => {
						uni.hideLoading()
						if (res.data.code == 200) {
							uni.navigateBack({
								delta: 1
							});
						} else {

							uni.showModal({
								title: '提示',
								content: res.data.info,
								showCancel: false,
								confirmText: "关闭",
							});
						}
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					}
				});
			}
		}
	}
}
